import { _decorator, Component, Node, Graphics, EventTouch, v3, Vec3, log, Vec2, v2 } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('level1_science')
export class level1_science extends Component {
    @property(Node)
    left: Node = null;
    @property(Node)
    right: Node = null;
    @property(Node)
    area: Node = null;
    @property(Node)
    draw: Node = null;
    @property(Node)
    fireWork: Node = null;
    private graphics: Graphics = null;
    private rightP: Vec2;
    start() {
        this.initGraphic();
        this.draw.on(Node.EventType.TOUCH_START, this.touchStart, this);
        this.draw.on(Node.EventType.TOUCH_MOVE, this.touchMove, this)
    }
    initGraphic(){
        this.graphics = this.draw.getComponent(Graphics);
        this.graphics.lineWidth = 10;

        let startP = this.left.getPosition();
        let endP = this.right.getPosition();
        // log("check endP", endP, startP);
        this.rightP = v2(endP.x, endP.y);
        let g = this.graphics;
        g.moveTo(startP.x, startP.y);
        g.lineTo(startP.x+2, startP.y+2);
        g.stroke();
    }
    touchStart(event: EventTouch){
        let startP = this.left.getPosition();
        let endP = this.right.getPosition();
        log("check endP", endP, startP);
        let pStart = event.getUILocation();
        let out = v3();
        this.area.inverseTransformPoint(out, v3(pStart.x, pStart.y, 0));
        let g = this.graphics;
        g.moveTo(out.x, out.y);
        g.stroke();
    }

    touchMove(event: EventTouch){
        let newP = event.getUILocation();
        log("check p", newP)
        this.checkCorrect(newP);
        let out = v3();
        this.area.inverseTransformPoint(out, v3(newP.x, newP.y, 0));
        let g = this.graphics;
        g.lineTo(out.x, out.y);
        g.stroke();
    }
    checkCorrect(p: Vec2){
        // log("check startP", this.rightP);
        let dx = Math.abs(p.x-this.rightP.x);
        let dy = Math.abs(p.y-this.rightP.y);
        if(dx <= 5 || dy<=5) {
            this.fireWork.active = true;
        }
    }

    update(deltaTime: number) {
        
    }
    clearDraw(){
        this.graphics.clear();
    }
}


